import * as React from 'react';
import { Box, Flex, Heading, Text, VStack } from '@chakra-ui/react';

const RlCard = ({ title, text, children, onClick }) => {
  const renderColorBars = () => {
    const colorBars = [
      { bg: 'rlBlack', w: '38%' },
      { bg: 'rlBlue', w: '24%' },
      { bg: 'rlGreen', w: '16%' },
      { bg: 'rlYellow', w: '12%' },
      { bg: 'rlIvory', w: '10%' },
    ];

    return (
      <Flex w="100%" height="7px" marginTop="auto!important">
        {colorBars.map((boxProps, index) => (
          <Box
            key={index}
            height="100%"
            {...boxProps}
            _groupHover={{ background: 'rlBlue' }}
          />
        ))}
      </Flex>
    );
  };

  return (
    <VStack
      backgroundColor="rlCardGrey"
      onClick={onClick}
      _hover={{ bg: 'rlBlue', cursor: 'pointer', color: 'white' }}
      data-group
      alignItems="left"
    >
      <VStack
        marginX="2em"
        marginTop="1em"
        marginBottom="2em"
        h="100%"
        alignItems="left"
      >
        {title && (
          <Heading as="h3" size="md" data-testid="title">
            {title}
          </Heading>
        )}
        {text && (
          <Text noOfLines="4" data-testid="content" fontWeight="light">
            {text}
          </Text>
        )}

        {children}
      </VStack>

      {renderColorBars()}
    </VStack>
  );
};

export default RlCard;
